body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: left;
  font: Helvetica, sans-serif;
}

.Toolbar {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  height: 80px;
  background-color: #c40000;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 17s linear;
          animation: App-logo-spin infinite 17s linear;
  height: 80px;
  pointer-events: none;
}

.Button {
  height: 40px;
}

.CTA {
  height: 60px;
  width: 160px;
}

.parentContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 100%;
}

.containerRight {
  width: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-content: center;
          align-content: center;
  text-align: center;
}

.containerLeft {
  width: 50%;
  display: -webkit-flex;
  display: flex;
  text-align: center;
  -webkit-align-content: center;
          align-content: center;
}

.img-left {
  height: 400px;
  width: 100%;
  background-color: #0003c4
}

.img-right {
  height: 400px;
  width: 100%;
  background-color: #00c469
}

.lefttext {
  text-align: center;
  margin-top: 190px;
}

.righttext {
  text-align: center;
  margin-top: 190px;
}

.Body {
  display: -webkit-flex;
  display: flex;
  height: 300px;
  background-color: #fffb00
}

.body-content {
  text-align: center;
  margin-top: 130px;
  width: 100%;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

